<template> </template>

<script>
import {getStorage} from '@/utils/storage';

export default {
	name: 'index',
	created() {
		this.checkLogin();
	},
	methods: {
		// 验证是否登录
		checkLogin() {
			if (!getStorage('accessToken')) {
				localStorage.clear();
				this.$router.push({
					name: 'login'
				});
				return true;
			}
			this.$router.push({
				name: 'home'
			});
		}
	}
};
</script>

<style scoped></style>



